"use client";

import { useEffect, useState } from "react";
import { jwtDecode } from "jwt-decode";
import { Heart, Droplet, Bell, MapPin, CheckCircle } from "lucide-react";

export default function DonorDashboard() {
  const [history, setHistory] = useState<any[]>([]);
  const [requests, setRequests] = useState<any[]>([]);
  const [donorId, setDonorId] = useState<string | null>(null);
  const [loading, setLoading] = useState(true);
  const [location, setLocation] = useState<{ lat: number; lng: number } | null>(null);
  const [available, setAvailable] = useState<boolean>(false);

  // Get donorId from JWT token
  useEffect(() => {
    const token = localStorage.getItem("token");

    if (token) {
      try {
        const decoded: any = jwtDecode(token);
        setDonorId(decoded.userId);
      } catch (err) {
        console.error("Invalid token:", err);
      }
    }
  }, []);

  // Fetch donation history
  useEffect(() => {
    if (!donorId) return;

    fetch(`http://localhost:5000/donor/history/${donorId}`)
      .then((res) => res.json())
      .then((data) => {
        setHistory(data.donations || []);
        setLoading(false);
      })
      .catch((err) => console.error(err));
  }, [donorId]);

  // Fetch urgent requests
  useEffect(() => {
    fetch("http://localhost:5000/donor/requests")
      .then((res) => res.json())
      .then((data) => setRequests(data.requests || []))
      .catch((err) => console.error(err));
  }, []);

  // ===============================
  // üìç Get Donor Location
  // ===============================
  const getLocation = () => {
    if (!navigator.geolocation) {
      alert("Location not supported by your browser.");
      return;
    }

    navigator.geolocation.getCurrentPosition(
      (pos) => {
        const coords = {
          lat: pos.coords.latitude,
          lng: pos.coords.longitude,
        };
        setLocation(coords);
        alert("Location captured successfully!");
      },
      () => alert("Location access denied.")
    );
  };

  // ===============================
  // ‚úÖ Mark Donor Available
  // ===============================
  const markAvailable = async () => {
    if (!donorId) return alert("Donor not found.");
    if (!location) return alert("Please share your location first.");

    const payload = {
      donorId,
      available: true,
      latitude: location.lat,
      longitude: location.lng,
    };

    const res = await fetch("http://localhost:5000/donor/availability", {
      method: "POST",
      headers: { "Content-Type": "application/json" },
      body: JSON.stringify(payload),
    });

    const data = await res.json();

    if (data.success) {
      setAvailable(true);
      alert("You are now AVAILABLE for donation.");
    } else {
      alert("Error: " + data.message);
    }
  };

  // ===============================
  // ‚ùå Mark Donor Unavailable
  // ===============================
  const markUnavailable = async () => {
    if (!donorId) return;

    const res = await fetch("http://localhost:5000/donor/availability", {
      method: "POST",
      headers: { "Content-Type": "application/json" },
      body: JSON.stringify({
        donorId,
        available: false,
      }),
    });

    const data = await res.json();

    if (data.success) {
      setAvailable(false);
      alert("You are marked as UNAVAILABLE.");
    }
  };

  return (
    <main className="min-h-screen bg-gradient-to-br from-white via-red-50 to-white p-8">

      {/* Header */}
      <header className="flex justify-between bg-red-600 text-white p-5 rounded-lg shadow">
        <h1 className="text-3xl font-bold flex items-center gap-2">
          <Heart className="w-8 h-8" /> Donor Dashboard
        </h1>

        <button
          onClick={() => {
            localStorage.removeItem("token");
            window.location.href = "/";
          }}
          className="bg-white text-red-600 px-4 py-2 rounded-lg hover:bg-red-100 font-semibold"
        >
          Logout
        </button>
      </header>

      {/* ACTION BUTTONS */}
      <div className="mt-6 flex gap-4 flex-wrap">

        {/* Share Location */}
        <button
          onClick={getLocation}
          className="flex items-center gap-2 bg-blue-600 text-white px-5 py-2 rounded-lg shadow hover:bg-blue-700"
        >
          <MapPin /> Share My Location
        </button>

        {/* Mark Available */}
        {!available ? (
          <button
            onClick={markAvailable}
            className="flex items-center gap-2 bg-green-600 text-white px-5 py-2 rounded-lg shadow hover:bg-green-700"
          >
            <CheckCircle /> Mark as Available
          </button>
        ) : (
          <button
            onClick={markUnavailable}
            className="flex items-center gap-2 bg-gray-600 text-white px-5 py-2 rounded-lg shadow hover:bg-gray-700"
          >
            ‚ùå Mark as Unavailable
          </button>
        )}
      </div>

      {/* Availability Status */}
      {available && (
        <p className="mt-3 text-green-700 font-bold">
          ‚úî You are currently AVAILABLE for donation
        </p>
      )}

      {/* Donation History */}
      <section className="mt-10">
        <h2 className="text-2xl font-bold mb-4 flex items-center gap-2">
          <Droplet className="text-red-600" /> Donation History
        </h2>

        {loading ? (
          <p className="text-gray-600">Loading...</p>
        ) : history.length === 0 ? (
          <p className="text-gray-600">No donation history yet.</p>
        ) : (
          <div className="bg-white p-6 rounded-xl shadow-lg space-y-4">
            {history.map((item, i) => (
              <div key={i} className="border-b pb-3 flex justify-between">
                <div>
                  <p className="text-lg font-semibold text-red-700">{item.hospital}</p>
                  <p className="text-sm text-gray-600">
                    {item.date?.slice(0, 10)} ‚Ä¢ {item.units} units
                  </p>
                </div>
                <span className="px-3 py-1 bg-green-100 text-green-700 rounded-full">
                  {item.status}
                </span>
              </div>
            ))}
          </div>
        )}
      </section>

      {/* Urgent Requests */}
      <section>
        <h2 className="text-2xl font-bold mt-10 mb-4 flex items-center gap-2">
          <Bell className="text-red-600" /> Urgent Blood Requests
        </h2>

        {requests.length === 0 ? (
          <p className="text-gray-600">No urgent requests available.</p>
        ) : (
          <div className="grid md:grid-cols-2 gap-6">
            {requests.map((req, idx) => (
              <div key={idx} className="bg-white p-6 rounded-xl shadow border-l-4 border-red-600">
                <p className="text-xl font-bold text-red-700">{req.bloodType}</p>
                <p className="text-gray-600">{req.unitsNeeded} units needed</p>
                <p className="font-semibold mt-2">{req.hospital}</p>
                <p className="text-sm text-gray-600">Urgency: {req.urgency}</p>
                <p className="text-sm text-gray-600">{req.locationKm} km away</p>
              </div>
            ))}
          </div>
        )}
      </section>

    </main>
  );
}
